import React, { useState, createRef, useContext, useEffect } from "react";
import {
  Button,
  Modal,
  Form,
  Input,
  Radio,
  Drawer,
  Select,
  DatePicker,
  TimePicker
} from "antd";
import { activities, materialLists, employees } from "../enums";
import { PlotViewer } from "../plot-viewer/plot-viewer";
import moment from "moment";
import { ContextStore } from "../utils/store";
const { Option } = Select;

export const CreateActivityForm = ({ job, visible, onCancel, onCreate }) => {
  const { state, dispatch } = useContext(ContextStore);
  const formRef = createRef();

  const [activity, setActivity] = useState();
  const [material, setMaterial] = useState();

  useEffect(() => {
    if (job) {
      setActivity(job.activity);
      setMaterial(job.material);
    }
  }, [job]);

  // console.log(selectedPlot);

  const selectActivity = activity => {
    setActivity(activity);
    setMaterial(null);
  };

  const onFinish = result => {
    // const job = {
    //   ...result,
    //   date: result.date.toDate().toISOString(),
    //   plot: selectedPlot
    // };
    // console.log(job);
    onCreate(result);
    // dispatch({ type: "addJob", job: job });
    // formRef.current.resetFields();
  };

  const onClose = () => {
    onCancel();
    formRef.current.resetFields();
  };

  return job ? (
    <>
      <Drawer
        visible={visible}
        title="Activiteit vastleggen"
        placement="bottom"
        onClose={onClose}
        height={"100%"}
      >
        <PlotViewer
          mapStyle={{
            height: "150px",
            marginBottom: "24px",
            borderRadius: "5px"
          }}
          plot={job.plot}
        ></PlotViewer>
        <Form
          layout="vertical"
          onFinish={onFinish}
          initialValues={{
            name: job.name,
            employee: job.employee,
            date: moment(job.date),
            activity: job.activity,
            material: job.material,
            time: moment(job.time, "HH : mm : ss")
          }}
          ref={formRef}
        >
          <Form.Item
            name="name"
            label="Naam perceel"
            rules={[
              {
                required: true,
                message: "Naam van het perceel is vereist!"
              }
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="employee"
            label="Medewerker"
            rules={[
              {
                required: true,
                message: "Medewerker is vereist!"
              }
            ]}
          >
            <Select>
              {Object.keys(employees).map(key => (
                <Option value={key}>{employees[key]}</Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item
            name="date"
            label="Datum"
            rules={[
              {
                required: true,
                message: "Datum is vereist!"
              }
            ]}
          >
            <DatePicker format={"DD-MM-YYYY"} style={{ width: "100%" }} />
          </Form.Item>

          <Form.Item name="activity" label="Activiteit">
            <Select onChange={selectActivity}>
              {Object.keys(activities).map(key => (
                <Option value={key}>{activities[key]}</Option>
              ))}
            </Select>
          </Form.Item>
          {activity ? (
            materialLists[activity] ? (
              <Form.Item name="material" label="Materiaal / Gewas">
                <Select value={material} onChange={setMaterial}>
                  {Object.keys(materialLists[activity]).map(key => (
                    <Option value={key}>{materialLists[activity][key]}</Option>
                  ))}
                </Select>
              </Form.Item>
            ) : null
          ) : null}
          {material ? (
            <Form.Item name="materialAmount" label="Hoeveelheid materiaal">
              <Input />
            </Form.Item>
          ) : null}
          <Form.Item
            name="time"
            label="Tijd besteed"
            rules={[
              {
                required: true,
                message: "Tijd besteed is vereist!"
              }
            ]}
          >
            <TimePicker
              style={{ width: "100%" }}
              defaultOpenValue={moment("00:00:00", "HH:mm:ss")}
            />
          </Form.Item>
          <Form.Item name="remark" label="Opmerking">
            <Input type="textarea" />
          </Form.Item>
          <Form.Item
            style={{
              textAlign: "right",
              right: 0,
              bottom: 0,
              width: "100%",
              borderTop: "1px solid #e9e9e9",
              padding: "10px 16px",
              background: "#fff",
              marginBottom: 0
            }}
          >
            <Button onClick={onClose} style={{ marginRight: 24 }}>
              Annuleren
            </Button>
            <Button type="primary" htmlType="submit">
              Vastleggen
            </Button>
          </Form.Item>
        </Form>
      </Drawer>
    </>
  ) : null;
};
