import React, { Component } from "react";
import "./App.css";
import Viewer from "./viewer/viewer";
import { OpenLayers } from "./open-layers/open-layers";
import { Route, Switch } from "react-router-dom";
import { Pic } from "./pic";
import { Plots } from "./plots";
import { ContextStoreProvider } from "./utils/store";
import { JobDetail } from "./job-detail";
import { Activity } from "./activity";

class App extends Component {
  render() {
    return (
      <div className="App">
        <ContextStoreProvider>
          <Switch>
            <Route exact path="/" component={Plots} />
            <Route exact path="/job/:number/activity" component={Activity} />
            <Route exact path="/job/:number" component={JobDetail} />
            <Route
              exact
              path="/create-plot"
              // render={() => {
              //   return (
              //     <Viewer promptInstall={this.props.promptInstall}></Viewer>
              //   );
              // }}
              component={Viewer}
            />
            <Route exact path="/pic" component={Pic} />
            <Route path="/v2" component={OpenLayers} />
          </Switch>
        </ContextStoreProvider>
      </div>
    );
  }
}

export default App;
