import React, { createRef, useContext, useState, useEffect } from "react";
import {
  Map,
  WMSTileLayer,
  FeatureGroup,
  GeoJSON,
  AttributionControl,
  ZoomControl,
  LayersControl,
  LayerGroup,
  TileLayer
} from "react-leaflet";
import { useParams } from "react-router";
import { ContextStore } from "../utils/store";
import "./styles.scss";
import { Link } from "react-router-dom";

import logo from "../images/logo-wopa.jpg";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStop, faStopwatch } from "@fortawesome/free-solid-svg-icons";
import { CreatePlotForm } from "../create-plot-form";
import { CreateActivityForm } from "../create-activity-form";

const { BaseLayer, Overlay } = LayersControl;

export const Activity = ({ history }) => {
  const { number } = useParams();
  const { state } = useContext(ContextStore);
  const [job, setJob] = useState();
  const [startTime] = useState(new Date().getTime());
  const [time, setTime] = useState("00 : 00 : 00");
  const [timer, setTimer] = useState();

  const mapRef = createRef();

  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    setTimer(
      setInterval(() => {
        // Get today's date and time
        var now = new Date().getTime();

        // Find the distance between now and the count down date
        var distance = now - startTime;

        // Time calculations for days, hours, minutes and seconds
        var days = Math.floor(distance / (1000 * 60 * 60 * 24));
        var hours = Math.floor(
          (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);

        setTime(
          `${hours.toString().length === 1 ? "0" : ""}${hours} : ${
            minutes.toString().length === 1 ? "0" : ""
          }${minutes} : ${seconds.toString().length === 1 ? "0" : ""}${seconds}`
        );
      }, 1000)
    );
  }, []);

  useEffect(() => {
    const tmp = state.jobs.find(job => job.jobNumber === number);
    setJob(tmp);
    mapRef.current.leafletElement.fitBounds(tmp.plot.bounds, {
      padding: [5, 5]
    });
  }, [state.jobs, number]);

  const openModal = () => {
    clearInterval(timer);
    setShowModal(true);
  };

  const handleCancel = () => {
    setShowModal(false);
  };

  const onCreate = () => {
    history.push("/");
  };

  return (
    <div className="map">
      <Link to={"/job/" + number}>
        <div className="like-leaflet logo">
          <img src={logo} alt="logo" />

          {/* <header>BEESPOT</header> */}
        </div>
      </Link>
      <div className="buttons">
        <div className="like-leaflet timer">
          <FontAwesomeIcon className="stop-watch" icon={faStopwatch} />
          <div class="show-timer">{time}</div>
          <FontAwesomeIcon
            onClick={openModal}
            className="stop-button"
            icon={faStop}
          />
        </div>
      </div>
      <Map
        ref={mapRef}
        className="activity-viewer"
        zoom={10}
        center={[51.84276079, 5.18380148]}
        zoomControl={false}
        attributionControl={false}
      >
        <AttributionControl position="topright" />
        <ZoomControl position="bottomright" />
        <LayersControl position="topright">
          {/* <BaseLayer name="Test">
                  <TileLayer
                    attribution="&amp;copy PDOK"
                    url="https://geodata.nationaalgeoregister.nl/luchtfoto/infrarood/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=Actueel_ortho25IR&STYLE=default&TILEMATRIXSET=EPSG:3857&TILEMATRIX={z}&TILEROW={x}&TILECOL={y}&FORMAT=image%2Fpng"
                  />
                </BaseLayer> */}
          <BaseLayer checked name="Luchtfoto">
            <LayerGroup>
              <WMSTileLayer
                url="https://geodata.nationaalgeoregister.nl/luchtfoto/rgb/wms?"
                layers="Actueel_ortho25"
              />
              {/* <TileLayer
                      url="https://geodata.nationaalgeoregister.nl/tiles/service/tms/1.0.0/lufolabels/EPSG:28992/{z}/{x}/{y}.png"
                      tms={true}
                    /> */}
            </LayerGroup>
          </BaseLayer>
          <BaseLayer name="Luchtfoto Infrarood">
            <WMSTileLayer
              url="https://geodata.nationaalgeoregister.nl/luchtfoto/infrarood/wms?"
              layers="Actueel_ortho25IR"
            />
          </BaseLayer>
          <BaseLayer name="OpenStreetMap">
            <TileLayer
              attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
          </BaseLayer>
        </LayersControl>
        {/* <BaseLayer checked name="OpenStreetMap">
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
        </BaseLayer> */}
        <WMSTileLayer
          url="https://geodata.nationaalgeoregister.nl/luchtfoto/rgb/wms?"
          layers="Actueel_ortho25"
        />
        {job ? (
          <FeatureGroup>
            <GeoJSON
              data={job.plot.feature}
              style={{ color: "#0088ff", fillOpacity: 0 }}
            />
          </FeatureGroup>
        ) : null}
      </Map>
      <CreateActivityForm
        job={{ ...job, time: time }}
        visible={showModal}
        onCreate={onCreate}
        onCancel={handleCancel}
      ></CreateActivityForm>
    </div>
  );
};
