import React, { useEffect, useContext, useState } from "react";
import { useParams } from "react-router";
import { ContextStore } from "../utils/store";
import { Header } from "../header";
import { PlotCard } from "../card";
import { Card } from "antd";
import { activities, materialLists } from "../enums";
import { Link } from "react-router-dom";
import moment from "moment";

const { Meta } = Card;

const selectFirstInnerMostArray = (array, index) => {
  if (Array.isArray(array[0])) {
    return selectFirstInnerMostArray(array[0], index);
  } else {
    return array[index];
  }
};

export const JobDetail = () => {
  const { number } = useParams();
  const { state } = useContext(ContextStore);
  const [job, setJob] = useState();

  useEffect(() => {
    const tmp = state.jobs.find(job => job.jobNumber === number);
    setJob(tmp);
    console.log("+++", job, state, number);
  }, [number]);

  return (
    <div>
      <Header></Header>
      <div style={{ padding: "80px 24px 0" }}>
        {job ? (
          <>
            <PlotCard
              job={job}
              style={{ width: "100%" }}
              mapStyle={{ height: "150px" }}
            >
              <Meta title={job.name} description={activities[job.activity]} />
            </PlotCard>
            {!job.plot.feature.features ? (
              <a
                href={
                  "https://www.google.com/maps/dir/?api=1&destination=" +
                  selectFirstInnerMostArray(
                    job.plot.feature.geometry.coordinates,
                    1
                  ) +
                  "," +
                  selectFirstInnerMostArray(
                    job.plot.feature.geometry.coordinates,
                    0
                  )
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                <Card
                  hoverable
                  style={{
                    width: "100%",
                    height: "50px",
                    lineHeight: "50px",
                    marginTop: "12px"
                  }}
                  bodyStyle={{
                    padding: "0"
                  }}
                >
                  Navigeer naar perceel
                </Card>
              </a>
            ) : (
              <a
                href={
                  "https://www.google.com/maps/dir/?api=1&destination=" +
                  selectFirstInnerMostArray(
                    job.plot.feature.features[0].geometry.coordinates,
                    1
                  ) +
                  "," +
                  selectFirstInnerMostArray(
                    job.plot.feature.features[0].geometry.coordinates,
                    0
                  )
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                <Card
                  hoverable
                  style={{
                    width: "100%",
                    height: "50px",
                    lineHeight: "50px",
                    marginTop: "12px"
                  }}
                  bodyStyle={{
                    padding: "0"
                  }}
                >
                  Navigeer naar perceel
                </Card>
              </a>
            )}
            <Link to={"/job/" + job.jobNumber + "/activity/"}>
              <Card
                hoverable
                style={{
                  width: "100%",
                  height: "50px",
                  lineHeight: "50px",
                  marginTop: "12px"
                }}
                bodyStyle={{
                  padding: "0"
                }}
              >
                Activiteit uitvoeren
              </Card>
            </Link>
            <Card
              style={{
                width: "100%",
                marginTop: "12px",
                marginBottom: "12px",
                textAlign: "left"
              }}
            >
              <p>Datum: {moment(job.date).format("DD-MM-YYYY")}</p>
              <p>Klantnaam: {job.customer}</p>
              <p>Klantnummer: {job.customerNumber}</p>
              <p>Opdracht nummer: {job.jobNumber}</p>
              <p>Adres: {job.adres}</p>
              <p>
                Materiaal:{" "}
                {job.activity && job.material && materialLists[job.activity]
                  ? materialLists[job.activity][job.material]
                  : null}
              </p>
              <p>Remark: {job.remark}</p>
            </Card>
          </>
        ) : null}
      </div>
    </div>
  );
};
