import React, { useState, createRef, useContext } from "react";
import {
  Button,
  Modal,
  Form,
  Input,
  Radio,
  Drawer,
  Select,
  DatePicker
} from "antd";
import { activities, materialLists, employees } from "../enums";
import { PlotViewer } from "../plot-viewer/plot-viewer";
import moment from "moment";
import { ContextStore } from "../utils/store";
const { Option } = Select;

export const CreatePlotForm = ({
  selectedPlot,
  visible,
  onCancel,
  onCreate
}) => {
  const [activity, setActivity] = useState();
  const [material, setMaterial] = useState();
  const { state, dispatch } = useContext(ContextStore);
  const formRef = createRef();

  console.log(selectedPlot);

  const selectActivity = activity => {
    setActivity(activity);
    setMaterial(null);
  };

  const onFinish = result => {
    const job = {
      ...result,
      date: result.date.toDate().toISOString(),
      plot: selectedPlot
    };
    console.log(job);
    onCreate(job);
    dispatch({ type: "addJob", job: job });
    // formRef.current.resetFields();
  };

  const onClose = () => {
    onCancel();
    formRef.current.resetFields();
  };

  return (
    <>
      <Drawer
        visible={visible}
        title="Opdracht aanmaken"
        placement="bottom"
        onClose={onClose}
        height={"100%"}
      >
        <PlotViewer
          mapStyle={{
            height: "150px",
            marginBottom: "24px",
            borderRadius: "5px"
          }}
          plot={selectedPlot}
        ></PlotViewer>
        <Form
          layout="vertical"
          onFinish={onFinish}
          initialValues={{
            employee: Object.keys(employees)[0],
            date: moment()
          }}
          ref={formRef}
        >
          <Form.Item
            name="name"
            label="Naam perceel"
            rules={[
              {
                required: true,
                message: "Naam van het perceel is vereist!"
              }
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="employee"
            label="Medewerker"
            rules={[
              {
                required: true,
                message: "Medewerker is vereist!"
              }
            ]}
          >
            <Select>
              {Object.keys(employees).map(key => (
                <Option value={key}>{employees[key]}</Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item name="date" label="Datum">
            <DatePicker format={"DD-MM-YYYY"} style={{ width: "100%" }} />
          </Form.Item>

          <Form.Item name="customer" label="Klantnaam">
            <Input />
          </Form.Item>
          <Form.Item name="customerNumber" label="Klantnummer">
            <Input />
          </Form.Item>
          <Form.Item name="adres" label="Adres">
            <Input />
          </Form.Item>
          <Form.Item
            name="jobNumber"
            label="Opdrachtnummer"
            rules={[
              {
                required: true,
                message: "Opdrachtnummer is vereist!"
              }
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item name="activity" label="Activiteit">
            <Select onChange={selectActivity}>
              {Object.keys(activities).map(key => (
                <Option value={key}>{activities[key]}</Option>
              ))}
            </Select>
          </Form.Item>
          {activity ? (
            materialLists[activity] ? (
              <Form.Item name="material" label="Materiaal / Gewas">
                <Select value={material} onChange={setMaterial}>
                  {Object.keys(materialLists[activity]).map(key => (
                    <Option value={key}>{materialLists[activity][key]}</Option>
                  ))}
                </Select>
              </Form.Item>
            ) : null
          ) : null}
          <Form.Item name="remark" label="Opmerking">
            <Input type="textarea" />
          </Form.Item>
          <Form.Item
            style={{
              textAlign: "right",
              right: 0,
              bottom: 0,
              width: "100%",
              borderTop: "1px solid #e9e9e9",
              padding: "10px 16px",
              background: "#fff",
              marginBottom: 0
            }}
          >
            <Button onClick={onClose} style={{ marginRight: 24 }}>
              Annuleren
            </Button>
            <Button type="primary" htmlType="submit">
              Aanmaken
            </Button>
          </Form.Item>
        </Form>
      </Drawer>
    </>
  );
};
