import React, { PureComponent, createRef } from "react";
import { Link } from "react-router-dom";

import "./viewer.css";
import "leaflet/dist/leaflet.css";
import {
  Map,
  TileLayer,
  WMSTileLayer,
  LayersControl,
  LayerGroup,
  ZoomControl,
  AttributionControl
} from "react-leaflet";
import produce from "immer";
import {
  createFeatureLayers,
  handleZoomEnd,
  handleDragEnd,
  initialState,
  registerWFS,
  createWfs,
  wfsServiceDropdown
} from "../utils/wfs-helper";

import logo from "../images/logo-wopa.jpg";
import locate from "../images/locate.svg";
import camera from "../images/camera.svg";
import download from "../images/download.svg";
import { Graph } from "../graph/graph";
import L from "leaflet";

import "@geoman-io/leaflet-geoman-free";
import "@geoman-io/leaflet-geoman-free/dist/leaflet-geoman.css";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave, faTimes, faCheck } from "@fortawesome/free-solid-svg-icons";
import { Modal } from "antd";
import { CreatePlotForm } from "../create-plot-form";

const { BaseLayer, Overlay } = LayersControl;

class Viewer extends PureComponent {
  mapRef = createRef();

  selectedLayer;

  constructor(props) {
    super(props);

    this.state = produce(initialState(), draft => {
      draft.lat = 51.84276079;
      draft.lon = 5.18380148;
      draft.selectedFeature = null;
      draft.selectedLayer = null;
      draft.selectedPlot = null;
      draft.showModal = false;
    });
  }

  componentDidMount() {
    registerWFS(
      this,
      createWfs(
        "BRP Gewaspercelen",
        "https://geodata.nationaalgeoregister.nl",
        "brpgewaspercelen",
        "brpgewaspercelen:brpgewaspercelen",
        14,
        undefined
      ),
      createWfs(
        "Basis registratie Kadaster",
        "https://geodata.nationaalgeoregister.nl",
        "kadastralekaartv3",
        "kadastralekaartv3:perceel",
        14,
        undefined
      ),
      createWfs(
        "Agrarisch Areaal Nederland (AAN)",
        "https://geodata.nationaalgeoregister.nl",
        "aan",
        "aan:aan",
        14,
        undefined
      )
    );

    let map = this.mapRef.current.leafletElement;

    map.pm.addControls({
      position: "bottomleft",
      drawCircle: false,
      editMode: false,
      dragMode: false,
      drawPolyline: false,
      drawCircleMarker: false,
      drawMarker: false
    });

    // window.addEventListener("resize", () => {
    //   const mapRef = this.mapRef;
    //   debugger;
    //   this.mapRef.current.height(window.innerHeight);
    //   this.mapRef.current.invalidateSize();
    // });
  }

  handleFeatureClick = e => {
    if (this.state.selectedLayer) {
      this.state.selectedLayer.pm.disable();
      this.mapRef.current.leafletElement.removeLayer(this.state.selectedLayer);
    }

    if (this.selectedLayer) {
      this.selectedLayer.setStyle({
        color: "blue",
        fillOpacity: 0
      });
    }
    this.selectedLayer = e.layer;
    this.selectedLayer.setStyle({
      color: "red",
      fillOpacity: 0.2
    });

    this.selectedLayer.bringToFront();

    console.log(e.layer.feature);

    const bounds = this.selectedLayer.getBounds();
    this.setState(
      produce(this.state, draft => {
        draft.selectedFeature = {
          feature: this.selectedLayer.feature,
          bounds: [
            [bounds.getSouthWest().lat, bounds.getSouthWest().lng],
            [bounds.getNorthEast().lat, bounds.getNorthEast().lng]
          ]
        };
        draft.selectedPlot = null;
        draft.selectedLayer = null;
      })
    );
  };

  selectPlot = () => {
    this.mapRef.current.leafletElement.fitBounds(
      this.state.selectedFeature.bounds
    );

    console.log(this.state.selectedFeature);

    this.selectedLayer.setStyle({
      color: "blue",
      fillOpacity: 0
    });

    const layer = new L.GeoJSON(this.state.selectedFeature.feature, {
      style: { color: "red", fillOpacity: 0 }
    }).addTo(this.mapRef.current.leafletElement);
    layer.setZIndex(99999);
    layer.pm.enable({ allowSelfIntersection: false });

    this.setState(
      produce(this.state, draft => {
        draft.selectedLayer = layer;
      })
    );
  };

  savePlot = () => {
    this.state.selectedLayer.pm.disable();
    const bounds = this.state.selectedLayer.getBounds();
    console.log(this.state.selectedLayer);
    this.setState(
      produce(this.state, draft => {
        draft.selectedPlot = {
          feature: this.state.selectedLayer.toGeoJSON(),
          bounds: [
            [bounds.getSouthWest().lat, bounds.getSouthWest().lng],
            [bounds.getNorthEast().lat, bounds.getNorthEast().lng]
          ]
        };
        draft.showModal = true;
      })
    );
  };

  cancelPlot = () => {
    this.state.selectedLayer.pm.disable();
    this.setState(
      produce(this.state, draft => {
        draft.selectedPlot = null;
        draft.selectedFeature = null;
        draft.selectedLayer = null;
      })
    );
    this.mapRef.current.leafletElement.stopLocate();
  };

  locateUser = () => {
    this.mapRef.current.leafletElement.locate({
      setView: true,
      watch: false,
      maxZoom: 17,
      enableHighAccuracy: true
    });
  };

  handleLocationFound = e => {
    const map = this.mapRef.current.leafletElement;
    var radius = e.accuracy / 2;
    L.marker(e.latlng)
      .addTo(map)
      .bindPopup("You are within " + radius + " meters from this point")
      .openPopup();
    L.circle(e.latlng, 1).addTo(map);
  };

  async takePicture() {}

  createJob = job => {
    this.props.history.push("/");

    this.setState(
      produce(this.state, draft => {
        draft.showModal = false;
      })
    );
  };

  handleCancel = () => {
    this.setState(
      produce(this.state, draft => {
        draft.showModal = false;
      })
    );
  };

  zoomEnd = () => {
    return e => {
      if (this.state.selectedLayer) {
        this.state.selectedLayer.bringToFront();
      }
      handleZoomEnd(this)(e);
    };
  };

  dragEnd = () => {
    return e => {
      if (this.state.selectedLayer) {
        this.state.selectedLayer.bringToFront();
      }
      handleDragEnd(this)(e);
    };
  };

  render() {
    const position = [this.state.lat, this.state.lon];
    return (
      <>
        <div className="map">
          <Link to="/">
            <div className="like-leaflet logo">
              <img src={logo} alt="logo" />

              {/* <header>BEESPOT</header> */}
            </div>
          </Link>
          <div className="wfs-dropdown">{wfsServiceDropdown(this)}</div>
          <div className="like-leaflet locate" onClick={this.locateUser}>
            <img src={locate} alt="locate" />
          </div>
          {/* <div
            className="like-leaflet install"
            onClick={this.props.promptInstall}
          >
            <img src={download} alt="install" />
          </div> */}
          {/* <Link to="/pic">
            <div className="like-leaflet camera-button">
              <img src={camera} alt="camera" />
            </div>
          </Link> */}

          {/* <div className="buttons">
            <div className="">
              <a>chart</a>
              <a>detail</a>
            </div>
          </div> */}
          {this.state.selectedFeature ? (
            !this.state.selectedLayer ? (
              <div className="buttons">
                <div
                  onClick={this.selectPlot}
                  className="like-leaflet select-plot"
                >
                  Selecteer perceel
                </div>
              </div>
            ) : (
              <div className="buttons">
                <div
                  onClick={this.cancelPlot}
                  className="like-leaflet save-button"
                >
                  <FontAwesomeIcon icon={faTimes} />
                </div>
                <div
                  onClick={this.savePlot}
                  className="like-leaflet save-button"
                >
                  <FontAwesomeIcon icon={faCheck} />
                </div>
              </div>
            )
          ) : null}

          <Map
            center={position}
            zoom={this.state.zoom}
            onZoomEnd={this.zoomEnd()}
            onDragEnd={this.dragEnd()}
            onClick={this.handleClick}
            onLocationfound={this.handleLocationFound}
            ref={this.mapRef}
            className="leaflet-map"
            zoomControl={false}
            attributionControl={false}
          >
            <AttributionControl position="topright" />
            <ZoomControl position="bottomright" />
            <LayersControl position="topright">
              {/* <BaseLayer name="Test">
                  <TileLayer
                    attribution="&amp;copy PDOK"
                    url="https://geodata.nationaalgeoregister.nl/luchtfoto/infrarood/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=Actueel_ortho25IR&STYLE=default&TILEMATRIXSET=EPSG:3857&TILEMATRIX={z}&TILEROW={x}&TILECOL={y}&FORMAT=image%2Fpng"
                  />
                </BaseLayer> */}
              <BaseLayer checked name="Luchtfoto">
                <LayerGroup>
                  <WMSTileLayer
                    url="https://geodata.nationaalgeoregister.nl/luchtfoto/rgb/wms?"
                    layers="Actueel_ortho25"
                  />
                  {/* <TileLayer
                      url="https://geodata.nationaalgeoregister.nl/tiles/service/tms/1.0.0/lufolabels/EPSG:28992/{z}/{x}/{y}.png"
                      tms={true}
                    /> */}
                </LayerGroup>
              </BaseLayer>
              <BaseLayer name="OpenStreetMap">
                <TileLayer
                  attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
              </BaseLayer>
              <BaseLayer name="Luchtfoto Infrarood">
                <WMSTileLayer
                  url="https://geodata.nationaalgeoregister.nl/luchtfoto/infrarood/wms?"
                  layers="Actueel_ortho25IR"
                />
              </BaseLayer>
              <Overlay name="BRP Gewaspercelen (WMS)">
                <WMSTileLayer
                  url="https://geodata.nationaalgeoregister.nl/brpgewaspercelen/wms?"
                  layers="brpgewaspercelen:brpgewaspercelen"
                  transparent={true}
                  format="image/png"
                  opacity={0.8}
                />
              </Overlay>
              {/* <Overlay name="gemiddelde toegevoegde waarde per hectare weer van voedselteelten in de akkerbouw (2011-2014)">
                <WMSTileLayer
                  url="http://geodata.rivm.nl/geoserver/wms?"
                  layers="dank:lei_l1a_gc_akkerbvoed"
                  transparent={true}
                  format="image/png"
                  opacity={0.8}
                />
              </Overlay> */}
              <Overlay checked name="Agrarisch Areaal Nederland (AAN)">
                <WMSTileLayer
                  url="https://geodata.nationaalgeoregister.nl/aan/wms?"
                  layers="aan"
                  transparent={true}
                  format="image/png"
                  opacity={0.8}
                />
              </Overlay>
              {/* <Overlay name="Actueel Hoogtebestand Nederland 3">
                <WMSTileLayer
                  url="https://geodata.nationaalgeoregister.nl/ahn3/wms"
                  layers="ahn3_05m_dsm"
                  transparent={true}
                  format="image/png"
                  opacity={0.8}
                />
              </Overlay> */}
              {/* <Overlay name="Bestand bodemgebruik">
                <WMSTileLayer
                  url="https://geodata.nationaalgeoregister.nl/bestandbodemgebruik2015/wms"
                  layers="bbg2015"
                  transparent={true}
                  format="image/png"
                  opacity={0.8}
                />
              </Overlay> */}
              {/* <Overlay checked name="geoJson">
                <GeoJSON data={this.geoJson} />
              </Overlay> */}
            </LayersControl>
            {createFeatureLayers(this, this.handleFeatureClick)}
          </Map>
        </div>
        {/* <div className="charts">
          <Graph />
        </div> */}
        <CreatePlotForm
          selectedPlot={this.state.selectedPlot}
          visible={this.state.showModal}
          onCreate={this.createJob}
          onCancel={this.handleCancel}
        ></CreatePlotForm>
      </>
    );
  }
}

export default Viewer;
