import React, { createRef, useEffect } from "react";
import {
  Map,
  TileLayer,
  WMSTileLayer,
  LayersControl,
  LayerGroup,
  ZoomControl,
  AttributionControl,
  FeatureGroup,
  GeoJSON
} from "react-leaflet";

import "./viewer.css";
import "leaflet/dist/leaflet.css";
import {} from "leaflet";

const { BaseLayer, Overlay } = LayersControl;

export const PlotViewer = ({ plot, mapStyle }) => {
  const mapRef = createRef();

  useEffect(() => {
    if (plot) {
      console.log(plot);
    }
    if (plot) {
      mapRef.current.leafletElement.fitBounds(plot.bounds, { padding: [5, 5] });
    }
  }, [plot]);

  return (
    <Map
      ref={mapRef}
      style={mapStyle}
      className="plot-viewer"
      zoom={10}
      center={[51.84276079, 5.18380148]}
      scrollWheelZoom={false}
      doubleClickZoom={false}
      closePopupOnClick={false}
      dragging={false}
      zoomSnap={false}
      zoomDelta={false}
      trackResize={false}
      touchZoom={false}
      zoomControl={false}
      layersControl={false}
      attributionControl={false}
    >
      {/* <BaseLayer checked name="OpenStreetMap">
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
        </BaseLayer> */}
      <WMSTileLayer
        url="https://geodata.nationaalgeoregister.nl/luchtfoto/rgb/wms?"
        layers="Actueel_ortho25"
      />
      {plot ? (
        <FeatureGroup>
          <GeoJSON
            data={plot.feature}
            style={{ color: "#0088ff", fillOpacity: 0 }}
          />
        </FeatureGroup>
      ) : null}
    </Map>
  );
};
